import Header from "./Header";
import ProductList from "./ProductList";
import Hero from './Hero';

export default function Home({cart,setCart}) {
      
      const addToCart = (product) => {
        setCart([...cart,product]);
      }

  return (
    <div>
        <Header/>
        <Hero/>
        <ProductList addToCart={addToCart}/>
    </div>
  )
}


