import React from 'react';
import weightlifting from '../images/weightlifting.jpg';

export default function Hero() {
  return (
    <div className='w-full h-screen flex' style={{backgroundImage:`url(${weightlifting})`, backgroundSize:'cover'}}>
        <div className='max-w-[1240px] p-8 mx-auto flex flex-col justify-center items-center'>
            <div className='text-white'>
                <h1>Fuel Your Fitness Journey with Premium Supplements</h1>
                <h1>Welcome to FitFuel Emporium, Where Strength Begins!</h1>
            </div>
        </div>
    </div>
  )
}
