import { Link } from "react-router-dom";
import React from 'react';

export default function Header() {
  return (
      <div className="bg-black w-full absolute">
        <div className="h-24 max-w-[1240px] mx-auto flex items-center justify-between px-8 text-white">
              <h1><Link to="/">FitFuel Emporium</Link></h1>
              <ul className="hidden md:flex">
                  <li className="p-4">
                      About
                  </li>
                  <li className="p-4">Products</li>
                  <li className="p-4"><Link to="/cart">Cart</Link></li>
                  <li className="p-4">Checkout</li>
              </ul>
          </div> 
      </div>
  )
}