import React, { useEffect, useState } from 'react';
import Header from './Header';

export default function Cart({cart, onRemoveItem}) {
    const [totalPrice,setTotalPrice] = useState(0);
    const [quantities,setQuantities] = useState({});

     //Update totalPrice whenever cart changes
     useEffect(()=>{
        let sum = 0;
        let newQuantities = {}
        cart.forEach(item=>{
            sum += item.price * (quantities[item.id] || 1);
            newQuantities[item.id] = quantities[item.id] || 1;
        });
        setTotalPrice(sum);
        setQuantities(newQuantities);
    },[cart,quantities]);

    const handleQuantityChange = (event,itemId) => {
        const newQuantities = {...quantities};
        newQuantities[itemId] = parseInt(event.target.value);
        setQuantities(newQuantities);
    }
    
  return (
    <div>
        <Header/>
        <div>
            <h2>Cart</h2>
            <div>
                {cart.map((item,index)=>(
                    <div key={index}>
                        <h1>{item.title}</h1>
                        <p>{item.price}</p>
                        <label>
                            Quantity:
                            <input
                            type="number"
                            min="1"
                            value={quantities[item.id] || 1}
                            onChange={(e)=>handleQuantityChange(e,item.id)}
                            />
                        </label>
                        <p>Subtotal: {item.price * (quantities[item.id] || 1)}</p>
                        <button onClick={() => onRemoveItem(item.id)}>Remove</button>
                    </div>
                ))}
                <h1>Total Price: ${totalPrice}</h1>
            </div>
        </div>
    </div>
  )
}
