import React from 'react';
import productData from '../data/productData.json';

export default function ProductList({addToCart}) {
  return (
    <div>
        <h1>Product List</h1>
        <div>
            {productData.map(product => (
                <div key={product.id}>
                    <h3>{product.title}</h3>
                    <p>Price: ${product.price}</p>
                    <div>
                        <img src={require(`../${product.imagePath}`)} alt="whiskey" className='w-[300px] h-[300px]'></img>
                    </div>
                    <button onClick={()=>addToCart(product)} className='border-2 border-black'>Add to Cart</button>
                </div>
            ))}
        </div>
    </div>
  )
}